/* -- GENERAL -- */

body {
  margin-top: 50px;
  font-family: 'proxima-nova', 'Raleway', Helvetica, sans-serif;
  font-size: 16px;
  background: url('assets/images/originals/tile.jpg') top left repeat;
}

.logged-in .navbar-fixed-top {
		top: 32px;
}

/* == GLOBAL ==*/

h1,h2,h3,h4,h5,h6 {
  -webkit-font-smoothing: antialiased;
  text-rendering: optimizeLegibility;
  font-weight: bold;
}

h1 {
  font-size: 3em;
  color: white;
}

h1,h2 {
  margin: 0 0 40px;
}

h3 {
  margin: 20px 0;
}

h4 {
  font-size: 18px;
}

p {
  margin: 0 0 15px;
}

p.lead {
  font-weight: normal;
}

a:link, a:visited {
  color: #dd5638;
}

a:hover {
  text-decoration: none;
  color: #c9302c;
}

button, input, .btn, a.btn {
  -webkit-font-smoothing: antialiased;
  text-rendering: optimizeLegibility;
  color: white;
}

.btn {
  font-weight: bold;
}

button.btn:focus {
  outline: none !important;
}

.btn-danger {
  background-color: #dd5638;
  border-collapse: #d95131;
}

.btn-success {
  /*background: #79b044;*/
  border: none;
	/* styles for stretch effect: */
	margin: auto;
	width: 175px;
	transition: color 0.2s 0.3s, background-color 0.3s ease-in-out, width 0.3s 0s;
	border-radius: 50px;
	border: 2px solid #2A3A3F;
	background-color: transparent;
	color: #2A3A3F;
}

.btn-success:hover {
  /*background: #6a9b3d;
  border-color: #6a9b3d;*/
	/* styles for stretch effect: */
	width:200px;
	color: #FDCD3B;
  background-color: #2A3A3F;
	border: 2px solid #2A3A3F;
}

img {
  max-width: 100%;
}

.modal-content {
  background: url('assets/images/originals/tile.jpg') top left repeat;
}

.badge.social {
  background: #ccc;
  font-size: 24px;
  height: 40px;
  width: 40px;
  text-align: center;
  line-height: 42px;
  margin: 0 5px 20px 0;
  padding: 0;
  -webkit-border-radius: 50px;
  -moz-border-radius: 50px;
